import { createApp } from 'vue'
import App from './App.vue'
import './registerServiceWorker'
import router from './router'
import store from './store'

import 'vuetify/styles'
import { createVuetify } from 'vuetify'
import * as components from 'vuetify/components'
import * as directives from 'vuetify/directives'
import { MotionPlugin } from '@vueuse/motion'

import { library } from '@fortawesome/fontawesome-svg-core'
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'
import { faBars, faCalendarCheck, faCheckToSlot, faClock, faCreditCard, faDiceD20, faGear, faLayerGroup, faMagnifyingGlass, faReceipt, faRectangleList  } from "@fortawesome/free-solid-svg-icons";

library.add( faClock, faRectangleList, faCreditCard, faDiceD20, faCheckToSlot, faLayerGroup, faMagnifyingGlass, faBars, faGear, faCalendarCheck, faReceipt);
const vuetify = createVuetify({
  components,
  directives,
})

createApp(App).component('fa', FontAwesomeIcon).use(MotionPlugin).use(vuetify).use(store).use(router).mount('#app')
